<template>
	<div class="body">
		<div class="company_name flex_between">
			<div class="company_name_left">
				<i class="icon iconfont icon-gongsixiangqinglogo"></i>
				<span>新增子基金</span>
			</div>
		</div>
		<div class="form flex_between">
			<div class="form_list flex_start">
				<div class="form_list_left">
					合伙企业
				</div>
				<div class="form_list_right">
					<el-select v-model="name" :remote-method="remoteMethod" @change="changeName" :loading="loading" filterable remote :clearable='true' reserve-keyword placeholder="请输入公司名称">
						<el-option v-for="item in tianYanBackData" :key="item.name" :label="item.name" :value="item.name">
						</el-option>
					</el-select>
				</div>
			</div>
			<div class="form_list flex_start">
				<div class="form_list_left">
					基金期限（年）
				</div>
				<div class="form_list_right fund_limit_time flex_between">
					<el-input v-model="fund_expire1" placeholder="" clearable>
					</el-input>
					<span>+</span>
					<el-input v-model="fund_expire2" placeholder="" clearable>
					</el-input>
					<span>+</span>
					<el-input v-model="fund_expire3" placeholder="" clearable>
					</el-input>
				</div>
			</div>
			<div class="form_list flex_start">
				<div class="form_list_left">
					基金简称
				</div>
				<div class="form_list_right">
					<el-input v-model="short_name" placeholder="请输入内容" clearable>
					</el-input>
				</div>
			</div>
			<div class="form_list flex_start">
				<div class="form_list_left">
					基金规模（万元）
				</div>
				<div class="form_list_right">
					<el-input v-model="fund_scale" placeholder="请输入内容" clearable maxlength='16'>
					</el-input>
				</div>
			</div>
			<div class="form_list flex_start">
				<div class="form_list_left">
					币种
				</div>
				<div class="form_list_right">
					<el-select v-model="unit" clearable placeholder="请选择">
					    <el-option v-for="item in coinList" :key="item.value" :label="item.label" :value="item.value">
					    </el-option>
					</el-select>
				</div>
			</div>
			<div class="form_list flex_start">
				<div class="form_list_left">
					负责合伙人
				</div>
				<div class="form_list_right">
					<el-select v-model="response_partner" filterable clearable placeholder="请选择">
					    <el-option v-for="item in allUserList" :key="item.id" :label="item.name" :value="item.id">
					    </el-option>
					</el-select>
				</div>
			</div>
			<div class="form_list flex_start">
				<div class="form_list_left">
					工商设立日期
				</div>
				<div class="form_list_right">
					<el-date-picker v-model="industry_date" type="date" placeholder="选择日期" format="yyyy年MM月dd日" value-format="yyyy-MM-dd" :editable='false'>
    					</el-date-picker>
				</div>
			</div>
			<div class="form_list flex_start">
				<div class="form_list_left">
					投研经理
				</div>
				<div class="form_list_right">
					<el-select v-model="manager" filterable clearable placeholder="请选择">
					    <el-option v-for="item in allUserList" :key="item.id" :label="item.name" :value="item.id">
					    </el-option>
					</el-select>
				</div>
			</div>
			<div class="form_list flex_start">
				<div class="form_list_left">
					数据创建人
				</div>
				<div class="form_list_right">
					<el-select v-model="creater" filterable clearable placeholder="请选择">
					    <el-option v-for="item in allUserList" :key="item.id" :label="item.name" :value="item.id">
					    </el-option>
					</el-select>
				</div>
			</div>
			<div class="form_list flex_start">
				<div class="form_list_left">
					管理公司全称
				</div>
				<div class="form_list_right">
					<el-input v-model="company" placeholder="请输入内容" clearable>
					</el-input>
				</div>
			</div>
			<div class="form_list flex_start">
				<div class="form_list_left">
					备案状态
				</div>
				<div class="form_list_right">
					<el-select v-model="filing_status" clearable placeholder="请选择">
					    <el-option v-for="item in allFilingStatus" :key="item.id" :label="item.name" :value="item.id">
					    </el-option>
					</el-select>
				</div>
			</div>
			<div class="form_list flex_start">
				<div class="form_list_left">
					私募基金备案编号
				</div>
				<div class="form_list_right">
					<el-input v-model="filing_number" placeholder="请输入内容" clearable>
					</el-input>
				</div>
			</div>
			<div class="form_list flex_start">
				<div class="form_list_left">
					协议到期日
				</div>
				<div class="form_list_right">
					<el-date-picker v-model="agreement_date" type="date" placeholder="选择日期" format="yyyy年MM月dd日" value-format="yyyy-MM-dd" :editable='false'>
    					</el-date-picker>
				</div>
			</div>
			<div class="form_list flex_start">
				<div class="form_list_left">
					是否隐藏
				</div>
				<div class="form_list_right flex_start">
					<div @click="changeHideStatus(1)" class="radio_btn cursor" :class="[is_hidden?'radio_btn_active':'']">
						是
					</div>
					<div @click="changeHideStatus(2)" class="radio_btn cursor" :class="[!is_hidden?'radio_btn_active':'']">
						否
					</div>
				</div>
			</div>
		</div>
		<!--表单按钮模块-->
		<div class="form_submit flex_center">
			<button @click="cancel">取消</button>
			<button @click="saveSubmit">保存</button>
		</div>
	</div>
</template>

<script>
	import { getBeiAnStatusApi,getUserlistApi,getSubscriptionFundListApi,getTianyanIdApi,saveSubFundInfoApi } from '../../config/api'
	import { dialogConfirm, dialogConfirmHasbtn,isValidNumber ,isValidMoneyLength,isValidDecimal} from '../../config/mUtils'
	
	export default {
		name: 'subFundAdd',
		data() {
			return {
				loading: false,
				tianYanBackData: [],
				allFilingStatus: [],
				allUserList: [],
				allSubscriptionFundList: [
					{
						label: '母基金基金',
						options: [ ]
					},
					{
						label: '自管基金',
						options: [ ]
					}
				],
				// 表单字段开始
				name: '',
				short_name: '',
				unit: 1,
				industry_date: '',
				company: '',
				agreement_date: '',
				fund_expire1: '',
				fund_expire2: '',
				fund_expire3: '',
				fund_scale: '',
				response_partner: '',
				manager: '',
				creater: '',
				filing_status: '',
				filing_number: '',
				is_hidden: 0,
				// 表单字段结束
				// 货币类型
				coinList: [
					{
						value: 1,
						label: '人民币'
					},
					{
						value: 2,
						label: '美元'
					}
				],
			}
		},
		created() {
			this.getBeiAnStatus()
			this.getAllUserList()
			this.getSubscriptionFundList()
		},
		mounted() {},
		components: {},
		methods: {
			saveSubmit(){
				var is_valid_number = this.isValidFundTime();
				if (is_valid_number) {
					var fund_expire = '';
					if(this.fund_expire2==''&&this.fund_expire3==''){
						fund_expire = this.fund_expire1;
					}else if(this.fund_expire2==''&&this.fund_expire3!=''){
						dialogConfirm("info", '基金期限不能为空!');
						return;
					}else if(this.fund_expire2!=''&&this.fund_expire3==''){
						fund_expire = this.fund_expire1 + '+' + this.fund_expire2;
					}else{
						fund_expire = this.fund_expire1 + '+' + this.fund_expire2 + '+' + this.fund_expire3;
					}
				} else {
					return
				}
				if (this.industry_date&&this.agreement_date&&!(this.industry_date < this.agreement_date)) {
					dialogConfirm("info", '协议到期日应大于工商设立日期');
					return
				}

				if(this.fund_scale!=''){
					if(isValidMoneyLength(parseInt(this.fund_scale))){
						dialogConfirm("info", '基金规模数据过大');
						return
					}
					if(!isValidDecimal(this.fund_scale)||parseFloat(this.fund_scale) < 0){
						dialogConfirm('info', '基金规模应大于等于0');
						return
					}
				}
				var info = {
					short_name: this.short_name,
					name: this.name,
					unit: this.unit,
					industry_date: this.industry_date,
					company: this.company,
					agreement_date: this.agreement_date,
					fund_expire: fund_expire,
					fund_scale: this.fund_scale,
					response_partner: this.response_partner,
					manager: this.manager,
					creater: this.creater,
					filing_status: this.filing_status,
					filing_number: this.filing_number,
					is_hidden: this.is_hidden,
				}
				if (this.name && this.short_name) {
					saveSubFundInfoApi({
						info: info,
					}).then(res => {
						if(res.data.msgNo == 1) {
							dialogConfirm("success", res.data.message);
							this.$router.go(-1)
						} else {
							dialogConfirm("info", res.data.message);
						}
					})
				} else{
					if (!this.name) {
						dialogConfirm("info", '合伙企业不能为空');
					} else{
						dialogConfirm("info", '基金简称不能为空');
					}
				}
			},
			// 判断基金期限是否有效
			isValidFundTime(){
				if (this.fund_expire1) {
					let is_valid_fund_expire1 = isValidNumber(this.fund_expire1);
					let is_valid_fund_expire2 = isValidNumber(this.fund_expire2);
					let is_valid_fund_expire3 = isValidNumber(this.fund_expire3);
					if(this.fund_expire2&&!is_valid_fund_expire2){
						dialogConfirm("info", '基金期限输入格式不对');
						return false
					}
					if(this.fund_expire3&&!is_valid_fund_expire3){
						dialogConfirm("info", '基金期限输入格式不对');
						return false
					}
					if (is_valid_fund_expire1) {
						return true
					} else{
						dialogConfirm("info", '基金期限输入格式不对');
						return false
					}
				} else{
					dialogConfirm("info", '基金期限不能为空');
					return false
				}
			},
			cancel(){
				this.$router.go(-1)
			},
			// 获取子基金认购基金列表
			getSubscriptionFundList(){
				getSubscriptionFundListApi({

				}).then(res => {
					// //console.log(res);
					if(res.data.msgNo == 1) {
						this.allSubscriptionFundList[0].options = Object.assign({}, res.data.payload.fof_fund);
						this.allSubscriptionFundList[1].options = Object.assign({}, res.data.payload.self_fund);
					} else {
						dialogConfirm("info", res.data.message);
					}
				})
			},
			// 获取备案状态
			getBeiAnStatus(){
				getBeiAnStatusApi({
					
				}).then(res => {
					if(res.data.msgNo == 1) {
						this.allFilingStatus = res.data.payload.record_state;
					} else {
						dialogConfirm("info", res.data.message);
					}
				})
			},
			// 获取所有人员列表
			getAllUserList(){
				getUserlistApi({
					number: 10000,
					page: 1
				}).then(res => {
					if(res.data.msgNo == 1) {
						this.allUserList = res.data.payload.data;
						this.creater = parseInt( window.localStorage.getItem('htcx_uid')) ;
					} else {
						dialogConfirm("info", res.data.message);
					}
				})
			},
			// 获取天眼查返回的数据
			remoteMethod(query) {
				if(query !== '') {
					this.loading = true;
					getTianyanIdApi({
						name: query
					}).then(res => {
						this.loading = false; 
						if(res.data.msgNo == 1) {
							if(res.data.payload.length == 0) {
								this.tianYanBackData = [{
									name: query
								}]
							} else {
								let newObj = {
									name: query
								}
								res.data.payload.unshift(newObj);
								this.tianYanBackData = res.data.payload;
							}

						}
					})
				} else {
					this.tianYanBackData = [];
				}
			},
			changeName(e){
				// //console.log(e);
				this.getTianYanId()
			},
			getTianYanId(){
				var data = this.tianYanBackData;
				for (var i = 0,max = data.length;i<max;i++) {
					if (data[i].name == this.company_name) {
						if (data[i].id) {
							this.tianyan_id = data[i].id;
						} else{
							this.tianyan_id = '';
						}
						return
					}
				}
			},
			changeHideStatus(e){
				if (e == 1) {
					this.is_hidden = 1;
				} else{
					this.is_hidden = 0;
				}
			}
		},
	}
</script>

<style scoped="scoped">
	.body {
		padding: 0 30px 30px 30px;
		font-size: 14px;
	}
	.company_name {
		height: 72px;
		border-bottom: 1px solid #EAEAEA;
	}
	.company_name_left {
		font-size: 18px;
		color: #33302D;
	}
	.company_name_left i {
		margin-right: 3px;
		color: #6292F4;
	}
	.form{
		flex-wrap: wrap;
		padding: 30px 0;
		color: #6F7071;
	}
	.form_list{
		display: inline-flex;
		margin-bottom: 19px;
	}
	.form_list_left{
		width: 120px;
		color: #6F7071;
	}
	.form_list_right{
		width: 260px;
    		height: 40px;
	}
	.radio_btn{
		width: 73px;
		height: 40px;
		margin-right: 38px;
		line-height: 40px;
		text-align: center;
		border-radius: 20px;
		background: #F3F6FB;
	}
	.radio_btn_active{
		color: white;
		background: #6292F4 !important;
	}
	.form_submit button{
		width: 94px;
		height: 36px;
		color: #6292F4;
		font-size: 14px;
		margin-right: 26px;
		border-radius: 20px;
		border: 1px solid #6292F4;
	}
	.form_submit button:nth-child(2){
		color: white;
		background: #6292F4;
	}
</style>


